import { Component, h, Listen, State } from "@stencil/core";


@Component({
    tag: "wxcc-time",
    styleUrl: "wxcc-time.css",
    shadow: true

})

export class wxccTime {
    @State() td = new Date()
    @State() fd = new Date()
    @State() toSelected: HTMLSelectElement
    @State() fromSelected: HTMLSelectElement
    @State() tTime: HTMLInputElement
    @State() tDate: HTMLInputElement
    @State() fTime: HTMLInputElement
    @State() fDate: HTMLInputElement
    @State() to: number
    @State() from: number
    @State() hide = true
    @State() tab = "create"
    @State() lupStamp: HTMLInputElement
    @State() tStamp: string

    onToSelect(zzz: string) {
        console.log(zzz)
        if (zzz === "from") {
            if (!this.fromSelected.value) {
                console.log(this.fDate.value + "T" + this.fTime.value)
                this.fd = new Date(this.fDate.value + "T" + this.fTime.value)

            } else if (this.fromSelected.value === "now") {
                this.fd = new Date(Date.now())
            } else {
                this.fd = new Date(Date.now() - +this.fromSelected.value)
            }
            // this.fDate.value = this.fd.toISOString().slice(0, 10)
            let month = ("0" + (this.fd.getMonth() + 1)).slice(-2, 3)
            let date = ("0" + (this.fd.getDate())).slice(-2, 3)
            // console.log(month)
            this.fDate.value = (this.fd.getFullYear() + "-" + month + "-" + date)
            this.fTime.value = this.fd.toTimeString().slice(0, 8)
            this.from = this.fd.valueOf() as number
        } else {
            if (!this.toSelected.value) {
                this.td = new Date(this.tDate.value + "T" + this.tTime.value)
            } else if (this.toSelected.value === "now") {
                this.td = new Date(Date.now())
            } else {
                this.td = new Date(Date.now() - +this.toSelected.value)
            }
            // this.tDate.value = this.td.toISOString().slice(0, 10)
            let month = ("0" + (this.td.getMonth() + 1)).slice(-2, 3)
            let date = ("0" + (this.td.getDate())).slice(-2, 3)
            // console.log(month)
            this.tDate.value = (this.td.getFullYear() + "-" + month + "-" + date)
            this.tTime.value = this.td.toTimeString().slice(0, 8)
            this.to = this.td.valueOf() as number
            // console.log(this.td.getDate())

        }
    }

    onManSet(zzz: string) {
        if (zzz === "from") {
            this.fromSelected.value = null
        } else {
            this.toSelected.value = null
        }
        this.onToSelect(zzz)
    }

    @Listen('wxccTimeWidget', { target: "body" })
    onToggle() {
        this.hide = !this.hide;
        this.tStamp = null
    }


    epochToTime() {
        this.tStamp = new Date(+this.lupStamp.value).toString()

    }


    render() {

        const hideIcon = <svg xmlns="http://www.w3.org/2000/svg" height="20" width="17.5" viewBox="0 0 640 512"><path d="M38.8 5.1C28.4-3.1 13.3-1.2 5.1 9.2S-1.2 34.7 9.2 42.9l592 464c10.4 8.2 25.5 6.3 33.7-4.1s6.3-25.5-4.1-33.7L525.6 386.7c39.6-40.6 66.4-86.1 79.9-118.4c3.3-7.9 3.3-16.7 0-24.6c-14.9-35.7-46.2-87.7-93-131.1C465.5 68.8 400.8 32 320 32c-68.2 0-125 26.3-169.3 60.8L38.8 5.1zm151 118.3C226 97.7 269.5 80 320 80c65.2 0 118.8 29.6 159.9 67.7C518.4 183.5 545 226 558.6 256c-12.6 28-36.6 66.8-70.9 100.9l-53.8-42.2c9.1-17.6 14.2-37.5 14.2-58.7c0-70.7-57.3-128-128-128c-32.2 0-61.7 11.9-84.2 31.5l-46.1-36.1zM394.9 284.2l-81.5-63.9c4.2-8.5 6.6-18.2 6.6-28.3c0-5.5-.7-10.9-2-16c.7 0 1.3 0 2 0c44.2 0 80 35.8 80 80c0 9.9-1.8 19.4-5.1 28.2zm51.3 163.3l-41.9-33C378.8 425.4 350.7 432 320 432c-65.2 0-118.8-29.6-159.9-67.7C121.6 328.5 95 286 81.4 256c8.3-18.4 21.5-41.5 39.4-64.8L83.1 161.5C60.3 191.2 44 220.8 34.5 243.7c-3.3 7.9-3.3 16.7 0 24.6c14.9 35.7 46.2 87.7 93 131.1C174.5 443.2 239.2 480 320 480c47.8 0 89.9-12.9 126.2-32.5zm-88-69.3L302 334c-23.5-5.4-43.1-21.2-53.7-42.3l-56.1-44.2c-.2 2.8-.3 5.6-.3 8.5c0 70.7 57.3 128 128 128c13.3 0 26.1-2 38.2-5.8z" /></svg>

        let mainContent = <div> <section class="widgets">
            <div>
                <label id="from">From: </label>
                <input type="date" onChange={this.onManSet.bind(this, "from")} ref={el => this.fDate = el}></input><input type="time" onChange={this.onManSet.bind(this, "from")} ref={el => this.fTime = el} ></input>
                <select ref={el => this.fromSelected = el} onChange={this.onToSelect.bind(this, "from")}>
                    <option></option>
                    <option value="600000">10 minutes ago</option>
                    <option value="3600000">1 hour ago</option>
                    <option value="86400000">1 day ago</option>
                    <option value="604800000">1 week ago</option>
                    <option value="2629800000">1 month ago</option>
                </select>
            </div>
            <br></br>
            <div>
                <label id="to">To: </label>
                <input type="date" onChange={this.onManSet.bind(this, "to")} ref={el => this.tDate = el}></input><input type="time" onChange={this.onManSet.bind(this, "to")} ref={el => this.tTime = el} ></input>
                <select ref={el => this.toSelected = el} onChange={this.onToSelect.bind(this, "to")}>
                    <option></option>
                    <option value="now">Now</option>
                    <option value="600000">10 minutes ago</option>
                    <option value="3600000">1 hour ago</option>
                    <option value="86400000">1 day ago</option>
                    <option value="604800000">1 week ago</option>
                </select>
            </div>
        </section>
            {/* <br></br> */}
            <section>
                <p>
                    from: "{this.from}"
                    <br></br>
                    to: "{this.to}"
                </p>

            </section>
        </div>

        if (this.tab == "lookup") {
            mainContent = <div><section id="lup">
                <label>Epoch Timestamp: &nbsp;</label>
                <input ref={el => this.lupStamp = el}></input>
                <button onClick={this.epochToTime.bind(this)}>Convert</button>
            </section>
                <section>
                    <p>Timestamp: <br></br><br></br><div id="tmstp">{this.tStamp}</div></p>
                </section>
            </div>
        }

        return [
            <div class={"frame" + (this.hide ? " hidden" : "")}>
                <header><span class="hideMe" onClick={this.onToggle.bind(this)}>{hideIcon}</span><div class="title">Time Widget</div></header>
                <section id="tabs">
                    <button class={this.tab != "lookup"? "active":""} onClick={() => this.tab = "create"}>Create</button>
                    <button class={this.tab == "lookup"? "active":""} onClick={() => this.tab = "lookup"}>Lookup</button>
                </section>

                {mainContent}
            </div>

        ]
    }
}